.board {
  margin: auto;
  margin-top: 15px;
  border-collapse: collapse;
}

.App {
  text-align: center;
}

#solve-btn {
  font-size: 18px;
  color: steelblue;
  width: 15%;
  height: 5%;
  margin: 20px;
  border-radius: 8px;
  box-shadow: 0 6px 8px 0 rgba(0, 0, 0, 0.24), 0 10px 30px 0 rgba(0, 0, 0, 0.19);
}

#clear-btn {
  position: absolute;
  bottom: 2%;
  right: 2%;
  color: steelblue;
  border-radius:8px;
  box-shadow: 0 6px 8px 0 rgba(0, 0, 0, 0.24), 0 10px 30px 0 rgba(0, 0, 0, 0.19);
}